import React from 'react';
import '../css/dulieufake2.css';
import Chart from './Chart2';

function DuLieuKhoLine() {



    const chartData = {
        labels: [
            "Thng 1",
            "Thng 2",
            "Thng 3",
            "Thng 4",
            "Thng 5",
            "Thng 6",
            "Thng 7",
            "Thng 8",
            "Thng 9",
            "Thng 10",
            "Thng 11",
            "Thng 12"],
        datasets: [
            {
                label: "S lng hng nhp",
                data: [33, 53, 85, 41, 44, 65, 33, 64, 12, 6, 12, 65],
                fill: true,
                borderColor: "#16CC0C"
            },
            {
                label: "S lng hng bn",
                data: [33, 25, 35, 51, 54, 76, 33, 23, 77, 35, 11, 89],
                fill: false,
                borderColor: "#FFEB24"
            },
            {
                label: "Tng doanh thu",
                data: [13, 21, 57, 32, 12, 12, 42, 43, 12, 43, 33, 12],
                fill: false,
                borderColor: "#FF0010",
            },

        ]
    };
    return (
        <>
            <div className="dulieufake2">
                <div className="dulieufake2-header">
                    <h5>Doanh thu hng thng</h5>
                </div>
                <Chart chartData={chartData} />

            </div>
        </>
    );
}

export default DuLieuKhoLine;
